import { useState } from "react";
import styles from "./Register.module.css"; // For custom styles

const Register = () => {
  const [status, setStatus] = useState("");

  const handleStatusClick = (status) => {
    setStatus(status); // Set the selected status
  };

  const handleSubmit = async (event) => {
    event.preventDefault();

    // Send the selected status to the API
    const response = await fetch("/api/sendWebhook", {
      method: "POST",
      body: JSON.stringify({ name: "John Doe", status }),
      headers: { "Content-Type": "application/json" },
    });

    if (response.ok) {
      alert("Status updated!");
    } else {
      alert("Error sending status.");
    }
  };

  return (
    <div className={styles.container}>
      <h1>Register Your Status</h1>
      <div className={styles.statusContainer}>
        <button
          onClick={() => handleStatusClick("Present")}
          className={`${styles.statusButton} ${status === "Present" ? styles.selected : ""}`}
        >
          Present
        </button>
        <button
          onClick={() => handleStatusClick("Late")}
          className={`${styles.statusButton} ${status === "Late" ? styles.selected : ""}`}
        >
          Late
        </button>
        <button
          onClick={() => handleStatusClick("Absent")}
          className={`${styles.statusButton} ${status === "Absent" ? styles.selected : ""}`}
        >
          Absent
        </button>
      </div>
      <button onClick={handleSubmit} className={styles.submitButton}>Submit</button>
    </div>
  );
};

export default Register;
